import React from 'react'
import { useFormik } from 'formik'
import * as Yup from 'yup'

export default function ContactForm() {
  const formik = useFormik({
    initialStatus: {},
    initialValues: {
      name: '',
      email: '',
      title: '',
      message: '',
    },
    validationSchema: Yup.object({
      name: Yup.string().required('  '),
      email: Yup.string()
        .email(' email')
        .required('  email'),
      message: Yup.string().required(' '),
    }),
    async onSubmit(values, { resetForm, setStatus }) {
      try {
        setStatus({})
        const url =
          'https://script.google.com/macros/s/AKfycbz9awMyTIRnZuJHqDgkDqHgEvq21-5ndQl9z-XVtmeuAWGbnUQ/exec'
        const resp = await fetch(
          `${url}?${new URLSearchParams(values).toString()}`
        ).then(r => r.json())
        resetForm()
        setStatus({ success: true })
      } catch (err) {
        setStatus({ error: err.message })
      }
    },
  })

  return (
    <div>
      {formik.status.success ? (
        <p>!</p>
      ) : (
        <form
          onSubmit={formik.handleSubmit}
          className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4"
        >
          <div className="mb-4">
            <label
              htmlFor="name"
              className="block text-gray-700 text-sm font-bold mb-2"
            >
                ()
            </label>
            <div>
              <input
                id="name"
                {...formik.getFieldProps('name')}
                className={`shadow appearance-none border${
                  formik.errors.name ? ' border-red-500' : ' '
                } rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline`}
              />
              {formik.errors.name && (
                <p className="text-red-500">{formik.errors.name}</p>
              )}
            </div>
          </div>

          <div className="mb-4">
            <label
              htmlFor="email"
              className="block text-gray-700 text-sm font-bold mb-2"
            >
               email ()
            </label>
            <div>
              <input
                id="email"
                {...formik.getFieldProps('email')}
                className={`shadow appearance-none border${
                  formik.errors.email ? ' border-red-500' : ' '
                } rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline`}
              />
              {formik.errors.email && (
                <p className="text-red-500">{formik.errors.email}</p>
              )}
            </div>
          </div>

          <div className="mb-4">
            <label
              htmlFor="title"
              className="block text-gray-700 text-sm font-bold mb-2"
            >
              
            </label>
            <div>
              <input
                id="title"
                {...formik.getFieldProps('title')}
                className={`shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline`}
              />
            </div>
          </div>

          <div className="mb-4">
            <label
              htmlFor="message"
              className="block text-gray-700 text-sm font-bold mb-2"
            >
               ()
            </label>
            <div>
              <textarea
                id="message"
                {...formik.getFieldProps('message')}
                className={`shadow appearance-none border${
                  formik.errors.message ? ' border-red-500' : ' '
                } rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline`}
              />
              {formik.errors.message && (
                <p className="text-red-500">{formik.errors.message}</p>
              )}
            </div>
          </div>

          <button
            type="submit"
            disabled={!formik.dirty || !formik.isValid || formik.isSubmitting}
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
          >
            
          </button>
          {formik.status.error && (
            <p className="text-red-500">{formik.status.error}</p>
          )}
        </form>
      )}
    </div>
  )
}
